import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { connect } from 'react-redux';
import classNames from 'classNames';

import Header from '../components/Header'
import Footer from '../components/Footer'
import Navigation from '../components/Navigation';
import styles from '../styles/layout.module.css';

class TemplateWrapper extends React.Component {

  constructor(props){
    super(props)
    this.state = {}
    this.onResize = this.onResize.bind(this);
    this.updateDimensions = this.updateDimensions.bind(this);
  }

  updateDimensions(){

    const h_diff = this.state.windowHeight - window.innerHeight;
    const w_diff = this.state.windowWidth - window.innerWidth;
    const diff = Math.sqrt(h_diff*h_diff + w_diff * w_diff);

    if (Number.isNaN(diff) || 100 < diff) {

    this.setState({
      windowWidth:  window.innerWidth,
      windowHeight: window.innerHeight,
    })
    }
  }

  onResize (){
    if (this.rqf) return
    if( typeof window !== 'undefined' )
      this.rqf = window.requestAnimationFrame(() => {
        this.rqf = null
        this.updateDimensions()
      })
  }

  componentDidMount () {
    this.updateDimensions()
    if( typeof window !== 'undefined' )
      window.addEventListener('resize', this.onResize, false)
  }

  componentWillUnmount () {
    if( typeof window !== 'undefined' )
      window.removeEventListener('resize', this.onResize)
  }

  render () {
    const { children, lockScreen } = this.props;
    const { windowWidth, windowHeight } = this.state;
    return (
  <div
    className={classNames({
      [`${styles.container}`]: true,
      [`${styles.locked}`]: lockScreen,
    })}
  >
    <Header />
    <Navigation
      links={[
        {to: '/', text: 'Home'},
        {to: '/places', text: 'Standorte'},
        {to: '/products/', text: 'Produkte'},
        {to: '/pricelist/', text: 'Preisliste'},
        {to: '/terms', text: 'Konditionen'},
        {to: '/contact', text: 'Kontakt'},
        {to: '/links', text: 'Links' }
      ]}
      backgroundImage={this.props.data.backgroundImage}
    />
    <main className={styles.content} lang="de">
      {children({...this.props, windowWidth, windowHeight})}
    </main>
    <Footer />
  </div>
)
  }
}

TemplateWrapper.propTypes = {
  children: PropTypes.func,
  lockScreen: PropTypes.bool,
}

const mapStateToProps = ({ showNav }) => {
  return { lockScreen: showNav }
}

const ConnectedTemplateWrapper = connect(mapStateToProps, {})(TemplateWrapper)

export const query = graphql`
  query NavImageQuery {
    backgroundImage: imageSharp(id: { regex: "/navBg/" }) {
      sizes(maxWidth: 2300) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`;

export default ConnectedTemplateWrapper 



